section.pricing {
  background: #007bff;
  font-family: 'Times New Roman', Times, serif;
 
}

.pricing .card {
  border: none;
  border-radius: 14px;
  transition: all 0.3s;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
}



.pricing .card-title {
  margin: 10px 0;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: bold;
}

.pricing .card-price {
  font-size: 50px;
  margin: 0;
}

.pricing .card-price .period {
  font-size: 12px;
}

.pricing ul li {
  margin-bottom: 15px;
}

.pricing .text-muted {
  opacity: 0.7;
}

.pricing .btn {
  font-size: 80%;
  border-radius: 50px;
  letter-spacing: 5px;
  font-weight: bold;
  padding: 15px;
  opacity: 0.7;
  transition: all 0.2s;
}


a{
  height: 50px;
  width: 100%;
}

.d-grid{
  border: none;
}


  .pricing .card:hover {
    margin-top: -15px;
    margin-bottom: 15px;
    box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.3);
  }

  .pricing .card:hover .btn {
    opacity: 1;
  }
